import "./Items.scss";
import Item from "../Item/Item";

const Items = ({calls, loading}) => {

  
  return (
    <section className='items'>
      <div className='items__container'>
        <ul className='items__header'>
          <li className='items__header-point items__point-tip'></li>
          <li className='items__header-point items__point-time'></li>
          <li className='items__header-point items__point-member'></li>
          <li className='items__header-point items__point-call'></li>
          <li className='items__header-point items__point-source'></li>
          <li className='items__header-point items__point-evaluation'></li>
          <li className='items__header-point items__point-duration'></li>
        </ul>
        <ul>
        {loading ? calls?.map((each) => (
          <Item key={each.id} each={each}/>
        )) : <h2> ....</h2>}
        </ul>
      </div>
    </section>
  );
};
export default Items;
